	module.exports = {"data":{"wordpressPost":{"title":"Delicious cocktails that won&#8217;t blow your diet","content":"<p>Party season is almost over. Well, in the official summer equals party season sense anyway. But let’s face it &#8211; the vast majority of us like a drink all year round.</p>\n<p>Given that, the healthier versions are the way to go. <strong>And yes &#8211; there really are healthy versions</strong>. Well, healthier anyway. Come on. That’s better than nothing right?</p>\n<p>Here six totally guilt-free cocktail recipes we love that you&#8217;ll love to. Well, we think you will anyway. Feel free to let us know if you don&#8217;t. And also, to share yours.</p>\n<p><strong>THE MASTER</strong><br />\n1.5 oz. <a href=\"https://casamigostequila.com\">Casamigos Blanco Tequila</a><br />\n2 Whole Strawberries<br />\n10 Blueberries<br />\n6 Granny Smith Apple Slices<br />\n5 Bartlett Pear Slices<br />\nPalm Full of Kale<br />\n4 Drops Flaxseed Oil<br />\nCombine all ingredients into a blender with ice and blend until texture is smooth. Pour mixture into a mason jar and serve fresh.</p>\n<img class=\"alignnone size-medium wp-image-1382\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103511/The-Master-200x300.jpg\" alt=\"\" width=\"200\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103511/The-Master-200x300.jpg 200w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103511/The-Master-768x1152.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103511/The-Master-683x1024.jpg 683w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103511/The-Master.jpg 1573w\" sizes=\"(max-width: 200px) 100vw, 200px\" />\n<p>&nbsp;</p>\n<p><strong>THE REVIVER</strong><br />\n2 oz. Casamigos Blanco Tequila<br />\n1.5 oz. Fresh Lemon Juice<br />\n1 oz. Honey<br />\n2 Fresh Ginger Slices<br />\nPinch of Bee Pollen<br />\n6 drops B-12<br />\nCombine all ingredients into tin shaker. Add ice. Shake vigorously for 8-10 seconds. Strain into collins glass. Add fresh ice. Garnish with pinch of cayenne.</p>\n<img class=\"alignnone size-medium wp-image-1383\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103610/The-Reviver-192x300.jpg\" alt=\"\" width=\"192\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103610/The-Reviver-192x300.jpg 192w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103610/The-Reviver-768x1197.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103610/The-Reviver-657x1024.jpg 657w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103610/The-Reviver.jpg 1513w\" sizes=\"(max-width: 192px) 100vw, 192px\" />\n<p>&nbsp;</p>\n<p><strong>THE FIGHTER</strong><br />\n1.5 oz. Casamigos Blanco Tequila<br />\n1 oz. Fresh Lemon Juice<br />\n.5 oz. Agave Nectar<br />\n.25 oz. Raw Ginger Juice<br />\nPinch of Cayenne Pepper<br />\nCombine all ingredients except cayenne pepper. Shake and strain over new ice into a rocks glass. Top with club soda and garnish with pinch of cayenne pepper.</p>\n<img class=\"alignnone size-medium wp-image-1378\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103022/The-Fighter-200x300.jpg\" alt=\"\" width=\"200\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103022/The-Fighter-200x300.jpg 200w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103022/The-Fighter-768x1152.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103022/The-Fighter-682x1024.jpg 682w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103022/The-Fighter.jpg 1572w\" sizes=\"(max-width: 200px) 100vw, 200px\" />\n<p>&nbsp;</p>\n<p><strong>DRINK YOUR GREENS<br />\n</strong>2 oz. Casamigos Blanco<br />\n2 oz. fresh green juice 0.5 oz. fresh lime juice<br />\n0.5 oz. agave nectar 8 to 10 mint leaves 2 cucumber slices</p>\n<p>Combine all ingredients into a tin shaker. Muddle mint and cucumber and shake vigorously for 8 to 10 seconds. Fine strain into rocks glass and add fresh ice. Garnish with cucumber slice and mint sprig.</p>\n<img class=\"alignnone size-medium wp-image-1374\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102358/Drink-Your-Greens-117x300.jpg\" alt=\"\" width=\"117\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102358/Drink-Your-Greens-117x300.jpg 117w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102358/Drink-Your-Greens-768x1973.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102358/Drink-Your-Greens-399x1024.jpg 399w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102358/Drink-Your-Greens.jpg 1242w\" sizes=\"(max-width: 117px) 100vw, 117px\" />\n<p>&nbsp;</p>\n<p><strong>SPICY ROOTS<br />\n</strong>2 oz. Casamigos Blanco<br />\n1 oz. fresh beet juice 1 oz. fresh lime juice<br />\n0.5 oz. agave nectar 4 basil leaves 2 serrano chili slices 2 dashes orange bitters ½ thick rim of Tajin salt mixture</p>\n<p>Combine all ingredients into a tin shaker. Muddle basil and serranos and shake vigorously for 8 to 10 seconds. Fine strain into rocks glass and add fresh ice. Garnish with basil leaf and serrano slice.</p>\n<img class=\"alignnone wp-image-1375\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102415/Spicy-Roots-215x300.jpg\" alt=\"\" width=\"200\" height=\"279\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102415/Spicy-Roots-215x300.jpg 215w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102415/Spicy-Roots-768x1073.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04102415/Spicy-Roots-733x1024.jpg 733w\" sizes=\"(max-width: 200px) 100vw, 200px\" />\n<p>&nbsp;</p>\n<p><strong>CASA CLEAN MARGARITA<br />\n</strong>2 oz. Casamigos Reposado<br />\n2 oz. fresh lime juice 0.5 oz. agave nectar<br />\n2 dashes grapefruit bitters 2 fresh ginger slices</p>\n<p>Combine all ingredients into a tin shaker. Muddle ginger and shake vigorously for 8 to 10 seconds. Fine strain into rocks glass and add fresh ice. Garnish with the half-moon grapefruit slice and lime wedge.</p>\n<img class=\"alignnone size-medium wp-image-1381\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103227/small-Casa-Clean-Margarita-194x300.jpg\" alt=\"\" width=\"194\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103227/small-Casa-Clean-Margarita-194x300.jpg 194w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103227/small-Casa-Clean-Margarita-768x1190.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103227/small-Casa-Clean-Margarita-661x1024.jpg 661w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103227/small-Casa-Clean-Margarita.jpg 1215w\" sizes=\"(max-width: 194px) 100vw, 194px\" />\n<p>&nbsp;</p>\n<p><em>Sip, sip, sipping. Image: Instagram/@lildarlinbar</em></p>\n<p><strong>Yep. It&#8217;s that easy.</strong></p>\n<p>Remember, drink sensibly though and we mean that. Over-doing it is no fun at the time and it&#8217;s a total punish the day after so take it easy.</p>\n<p>And just in case you&#8217;re wondering, yes, <a href=\"https://casamigostequila.com\">Casamigos Tequila </a>is indeed the tequila co-owned by George Clooney and Rande Gerber, husband of Cindy Crawford. While we&#8217;re at it, George Clooney is the husband of Amal Clooney.</p>\n<p>Phew. That was fun writing that.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/04103610/The-Reviver.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"06bc369e-fcf3-5f1a-8c3e-70112d592e0c"}}

